
#thema-accessor {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    /* width: 734px;
    height: 300px; */
    left: 100px;
    top: 100px
}

#thema-accessor>.operation {
    background-color: rgba(146, 206, 255, 0.068);
    height: 34px;
    text-align: right;
}
#thema-accessor>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 4px 5px 0 0;
    border: solid 1px rgba(255, 255, 255, 0.651);
    border-radius: 3px;
    text-align: center;
}
#thema-accessor>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}

#thema-accessor>.operation>div>span {
    display: inline-block;
    margin: 2px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}

