#samplertab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 92, 153);
    text-align: left;
}

#samplertab-root>div {
    display: inline-block;
    width: calc(100% - 6px);
    background-color: rgb(33, 112, 161);
    margin-left: 3px;
}

#samplertab-root>.loader {
    margin-top: 3px;
    height: 35px;
}

#samplertab-root>.list {
    margin-top: 3px;
    height: calc(100% - 205px);
    overflow-y: auto;
}
#samplertab-root>.list>.record {
    display: inline-block;
    width: 100%;
    height: 35px;
    margin-top: 2px;
    background-color: rgba(255, 240, 32, 0.082);
}
#samplertab-root>.list>.record>* {
    vertical-align: top;
}
#samplertab-root>.list>.record>.label {
    display: inline-block;
    width: 80px;
    height: 31px;
    margin-top: 2px;
    margin-left: 4px;
    padding: 0 10px 0 10px;
    font-size: 24px;
    font-weight: 600;
    color: rgb(241, 255, 41);
    background-color: rgb(103, 123, 150);
    border: solid 1px rgba(233, 230, 230, 0.199);
    box-sizing: border-box;
    text-align: center;
}
#samplertab-root>.list>.record>input:nth-child(2) {
    display: inline-block;
    width: 16px;
    height: 100%;
    margin: 0 0 0 5px;
}
#samplertab-root>.list>.record>input:nth-child(3) {
    display: inline-block;
    width: calc(100% - 114px);
    height: 31px;
    margin: 2px 0 0 5px;
    background-color: rgb(120, 151, 192);
    padding-left: 2px;
    color: blue;
    font-size: 18px;
    border-radius: 2px;
    border: solid 1px rgb(0, 53, 102);
    box-sizing: border-box;
}

#samplertab-root>.operation {
    margin-top: 3px;
    height: 35px;
}

#samplertab-root>.keyboad {
    margin-top: 3px;
    height: 120px;
}


#samplertab-root>*>button {
    display: inline-block;
    min-width: 120px;
    height: 31px;
    margin-top: 2px;
    margin-left: 4px;
    padding: 0 10px 0 10px;
    font-size: 22px;
    font-weight: 600;
    color: rgb(233, 230, 230);
    background-color: rgb(103, 123, 150);
    border: solid 1px rgb(233, 230, 230);
    box-sizing: border-box;
    border-radius: 4px;
    text-align: center;
}
#samplertab-root>*>button:hover {
    background-color: rgb(158, 182, 214);
}