#scorecont-thematab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    text-align: left;
}

#scorecont-thematab-root>.cont {
    display: inline-block;
    height: 100%;
    vertical-align: top;
    background-color: #e7e7e7;
    text-align: left;
}

/* 左側のコンポーネント */
#scorecont-thematab-root>.left {
    width: calc(100% - 550px - 4px);
    margin-right: 2px;
}

#scorecont-thematab-root>.left>div{
    width: calc(100% - 6px);
    margin-left: 3px;
    margin-top: 3px;
}
#scorecont-thematab-root>.left>.loader {
    height: 40px;
    background-color: rgba(230, 230, 230, 0.226);
}
#scorecont-thematab-root>.left>.tree {
    height: calc(100% - 92px);
    /* background-color: rgb(255, 242, 206); */
    background-color: rgb(226, 226, 226);
    overflow: auto;
}
#scorecont-thematab-root>.left>.tree.lock {
    background-color: rgba(0, 0, 121, 0.151);
    pointer-events: none;
}
#scorecont-thematab-root>.left>.tree>.item {
    display: inline-block;
    min-width: 100%;
    height: 35px;
    margin: 2px 0 0 0;
    white-space: nowrap;
    opacity: 0.5;
    /* background-color: rgba(38, 0, 255, 0.103); */
}
#scorecont-thematab-root>.left>.tree>.item.active {
    background-color: rgba(255, 255, 255, 0.514);
    opacity: 1;
}
#scorecont-thematab-root>.left>.tree>.item.lock {
    /* background-color: rgba(0, 0, 0, 0.411); */
    opacity: 0.2;
}

#scorecont-thematab-root>.left>.tree>.item>div {
    display: inline-block;
    height: 100%;
    vertical-align: top;
}
#scorecont-thematab-root>.left>.tree>.item>.indent {
    /* background-color: rgba(255, 255, 255, 0.164); */
}

#scorecont-thematab-root>.left>.tree>.item>.opener {
    background-color: rgba(255, 255, 255, 0.685);
    border: solid 1px black;
    width: 27px;
    height: 27px;
    border-radius: 2px;
    margin: 4px 4px 0 0;
}
#scorecont-thematab-root>.left>.tree>.item>.opener:hover {
    background-color: rgba(252, 0, 0, 0.911);
}
#scorecont-thematab-root>.left>.tree>.item>.opener>span {
    display: inline-block;
    margin: 2px 0 0 4px;
    color: black;
    font-size: 18px;
    font-weight: 600;
}

#scorecont-thematab-root>.left>.tree>.item>.type {
    background-color: rgba(255, 255, 255, 0.068);
    padding: 12px 4px 0 4px;
    box-sizing: border-box;
    border-radius: 3px;
}
#scorecont-thematab-root>.left>.tree>.item>.type>span {
    font-size: 12px;
    font-weight: 600;
    color: black;
}
#scorecont-thematab-root>.left>.tree>.item>.type.root>span {
    color: rgb(177, 0, 0);
}
#scorecont-thematab-root>.left>.tree>.item>.type.sustain>span {
    color: rgb(0, 12, 173);
}
#scorecont-thematab-root>.left>.tree>.item>.type.pattern>span {
    color: rgb(0, 155, 46);
}
#scorecont-thematab-root>.left>.tree>.item>.type.category>span {
    color: rgb(199, 106, 0);
}
#scorecont-thematab-root>.left>.tree>.item>.type.condition>span {
    color: rgb(209, 0, 146);
}
#scorecont-thematab-root>.left>.tree>.item>.type.voicing>span {
    color: rgb(0, 180, 171);
}

#scorecont-thematab-root>.left>.tree>.item>.label {
    background-color: rgba(255, 255, 255, 0.192);
    padding: 4px 10px 0 10px;
    box-sizing: border-box;
    min-width: 100px;
    border: solid 1px rgba(0, 0, 0, 0.144);
    border-radius: 3px;
}
#scorecont-thematab-root>.left>.tree>.item>.label:hover {
    background-color: rgba(255, 255, 255, 0.349);
}
#scorecont-thematab-root>.left>.tree>.item>.label.active {
    background-color: rgba(255, 238, 0, 0.966);
    box-shadow: 2px 2px 15px 4px #000b3a48;
}
#scorecont-thematab-root>.left>.tree>.item>.label>span {
    font-size: 20px;
    color: rgb(0, 0, 0);
}
#scorecont-thematab-root>.left>.tree>.item>.label>span.create {
    color: rgb(197, 0, 0);
    font-weight: 600;
}
#scorecont-thematab-root>.left>.tree>.item>.label>span.value {
    color: rgb(0, 0, 0);
    font-weight: 600;
}

#scorecont-thematab-root>.left>.operation {
    height: 40px;
    background-color: rgba(230, 230, 230, 0.226);
}

/* 右側のコンポーネント */
#scorecont-thematab-root>.right {
    width: 550px;
    margin-left: 2px;
}

#themainfotab-root {
    width: 100%;
    height: 100%;
}

#themainfo-tabs {
    display: inline-block;
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    margin: 5px 0 0 5px;
}

.thematab-detail {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: rgb(153, 153, 153);
    text-align: left;
}

.thematab-detail>.record {
    display: inline-block;
    width: 100%;
    height: 35px;
    margin-top: 2px;
    /* background-color: rgba(255, 240, 32, 0.082); */
    text-align: left;
}
.thematab-detail>.record>* {
    vertical-align: top;
}

.thematab-detail>.record>.label {
    display: inline-block;
    width: 140px;
    height: 25px;
    margin-top: 5px;
    margin-left: 4px;
    padding: 0 10px 0 10px;
    line-height: 24px;
    font-size: 20px;
    font-weight: 600;
    color: rgb(255, 248, 248);
    background-color: rgba(196, 196, 196, 0.726);
    /* border-bottom: solid 1px rgba(233, 230, 230, 0.726); */
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
}
.thematab-detail>.record>.fixed {
    display: inline-block;
    width: 240px;
    height: 25px;
    margin-top: 5px;
    margin-left: 4px;
    padding: 0 2px 0 2px;
    line-height: 24px;
    font-size: 20px;
    font-weight: 600;
    color: rgb(0, 22, 221);
    background-color: rgba(196, 196, 196, 0.5);
    /* border-bottom: solid 1px rgba(233, 230, 230, 0.726); */
    /* border-radius: 5px; */
    /* box-sizing: border-box; */
    text-align: left;
}

.thematab-detail>.record>input {
    display: inline-block;
    width: calc(100% - 152px);
    height: 31px;
    margin: 2px 0 0 4px;
    background-color: rgb(206, 206, 206);
    padding-left: 2px;
    color: rgb(48, 48, 48);
    font-size: 18px;
    font-weight: 600;
    border-radius: 2px;
    /* border: solid 1px rgb(255, 255, 255); */
    box-sizing: border-box;
    border-radius: 5px;
}

.thematab-detail>.record>select {
    display: inline-block;
    width: 240px;
    height: 31px;
    margin: 2px 0 0 4px;
    background-color: rgb(206, 206, 206);
    padding-left: 2px;
    color: rgb(48, 48, 48);
    font-size: 18px;
    font-weight: 600;
    border-radius: 2px;
    /* border: solid 1px rgb(255, 255, 255); */
    box-sizing: border-box;
    border-radius: 5px;
}
.thematab-detail>.record>select>option {
    background-color: rgb(172, 172, 172);
}
.thematab-detail>.record>select>option:hover {
    background-color: rgb(206, 206, 206);
}


.thematab-detail>*>button {
    display: inline-block;
    /* min-width: 120px; */
    height: 31px;
    margin-top: 2px;
    margin-left: 4px;
    padding: 0 10px 0 10px;
    font-size: 22px;
    font-weight: 600;
    color: rgb(194, 194, 194);
    background-color: rgb(116, 116, 116);
    border: solid 2px rgb(206, 206, 206);
    box-sizing: border-box;
    border-radius: 4px;
    text-align: center;
}
.thematab-detail>*>button:hover {
    background-color: rgb(150, 148, 148);
}

.thematab-detail>.src{
    display: inline-block;
    width: calc(100% - 10px);
    height: 200px;
    margin: 5px 0 0 5px;
    background-color: rgb(105, 105, 105);
}

.thematab-detail>.src>textarea {
    display: inline-block;
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    background-color: rgb(197, 219, 217);
    margin: 5px 0 0 5px;
    resize: none;
    font-size: 16px;
    color: black;
}

.thematab-detail>.selector {
    display: inline-block;
    width: calc(100% - 10px);
    height: 78px;
    margin: 5px;
    background-color: rgb(66, 63, 114);
    border: solid 1px white;
    text-align: left;
}

.thematab-detail>.selector>.item {
    display: inline-block;
    /* padding: 0 10px 0 10px; */
    width: 124px;
    height: 30px;
    font-size: 20px;
    font-weight: 600;
    /* background-color: black; */
    color: rgb(255, 255, 255);
    border: solid 1px white;
    margin: 5px 0 0 4px;
    text-align: center;
    line-height: 24px;
    border-radius: 10px;
}
.thematab-detail>.selector>.item:hover {
    background-color: rgba(0, 0, 0, 0.295);
}
.thematab-detail>.selector>.item.active {
    background-color: rgb(0, 0, 0);
    color: yellow;
}
.thematab-detail>.selector>.item.active:hover {
    background-color: rgba(0, 0, 0, 0.404);
}